
.search-form {
 display: flex;
 justify-content: center;
 gap: 20px;
 margin: 0 auto;
 padding-top: 13px;
}

.body {
    margin: 0;
}

.input-form {
    font-size: 14px;
  line-height: 1.5;
  letter-spacing: 0.04em;
  height: 20px;
  width: 250px;
  padding: 8px 16px;
  border-radius: 4px;
  border: 1px solid grey;
  box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15);
  outline: grey;
  background-color: white;
  filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.15));
  transition: border 250ms cubic-bezier(0.4, 0, 0.2, 1);
  padding-right: 55px;
}

.input-form:focus {
    border: 1px solid orange;
}


.input-container {
    position: relative;
  }


.header {
    margin: 0 auto;
    background-color: #c8df09;
    width: 100%;
    padding-bottom: 15px;
    position: sticky;
    top: 0
}

.btn-submit {
    width: 50px;
    height: 38px;
    cursor: pointer;
    background-color: hsl(260deg 11% 85% / 32%);;
    border: 0;
    border-radius: 4px;
    justify-content: center;
    align-items: center;
    font-size: 15px;
    display: flex;
    position: absolute;
    transform: translate(136px);

