.load-more {
    display: none;
}

.visible {
    font-size: 15px;
    align-items: center;
    height: 40px;
    display: flex;
    justify-content: center;
    margin: 0 auto; 
    margin-top: 25px;
    margin-bottom: 25px;
    width: 150px;
    border-radius: 4px;
    border: 0;
    cursor: pointer;
    border: 1px solid rgb(92, 88, 88);
    transition: background-color 300ms cubic-bezier(0.4, 0, 0.2, 1);
}

.visible:hover {
    background-color: blanchedalmond;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.15);
}

.photo-card {
border: 1px solid rgb(173, 170, 170);
box-shadow: 5px 5px 5px 1px rgba(0, 0, 0, 0.3);
border-radius: 5px;


}
.info {
    display: flex;
    gap: 15px;
    justify-content: center;
    
    
}

.gallery {
    display: inline-flex;
    gap: 35px;
    flex-wrap: wrap;
    margin-top: 20px;
    justify-content: center;
    margin-bottom: 20px
}


.info-item {
font-size: 20px;
display: flex;
margin-bottom: 15px;

}

.descr {
    display: flex;
    justify-content: center;
    font-size: 15px;
    font-weight: normal;
    margin-top: 15px;
}